<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Pengguna</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Cari Pengguna</ion-label>
          <ion-input
            v-model="searchQuery"
            placeholder="Masukkan nama pengguna"
          ></ion-input>
        </ion-item>
        <ion-button style="color: white" expand="block" @click="filterUsers"
          >Cari</ion-button
        >
        <ion-item>
          <input type="file" accept=".xlsx, .xls" @change="handleFileUpload" />
        </ion-item>

        <ion-button style="color: white" expand="block" @click="importFromExcel">
          Import Pengguna dari Excel
        </ion-button>


        <div class="table-container">
          <ion-grid>
            <ion-row>
              <ion-col>
                <h2>Daftar Pengguna</h2>
                <div class="table-wrapper">
                  <table>
                    <thead>
                      <tr>
                        <th>Nama Lengkap</th>
                        <th>Username</th>
                        <th>Email</th>
                        <th>Role</th>
                        <th>QR Code</th>
                        <th>Status</th>
                        <th>Aksi</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="user in filteredUsers" :key="user.id">
                        <td>{{ user.fullName }}</td>
                        <td>{{ user.username }}</td>
                        <td>{{ user.email }}</td>
                        <td>{{ user.role }}</td>
                        <td>{{ user.qrCode }}</td>
                        <td>{{ user.status }}</td>
                        <td>
                          <ion-button
                            style="color: white"
                            @click="editUser(user)"
                            >Edit</ion-button
                          >
                          <ion-button
                            @click="changeStatus(user)"
                            :color="
                              user.status === 'aktif' ? 'danger' : 'success'
                            "
                          >
                            {{
                              user.status === "aktif"
                                ? "Nonaktifkan"
                                : "Aktifkan"
                            }}
                          </ion-button>
                          <ion-button
                            style="color: white"
                            @click="resetPassword(user.email)"
                            >Reset Password</ion-button
                          >
                          <ion-button
                            color="danger"
                            @click="deleteUser(user.id)"
                            >Hapus</ion-button
                          >
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </ion-col>
            </ion-row>
          </ion-grid>
        </div>
        <ion-button style="color: white" expand="block" @click="exportToExcel"
          >Export ke Excel</ion-button
        >
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>

      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Edit Pengguna</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-item v-if="selectedUser">
            <ion-label>Nama Lengkap</ion-label>
            <ion-input v-model="selectedUser.fullName"></ion-input>
          </ion-item>
          <ion-item v-if="selectedUser">
            <ion-label>Username</ion-label>
            <ion-input v-model="selectedUser.username"></ion-input>
          </ion-item>
          <ion-item v-if="selectedUser">
            <ion-label>Email</ion-label>
            <ion-input v-model="selectedUser.email"></ion-input>
          </ion-item>
          <ion-item v-if="selectedUser">
            <ion-label>Role</ion-label>
            <ion-select v-model="selectedUser.role">
              <ion-select-option value="admin">Admin</ion-select-option>
              <ion-select-option value="siswa">Siswa</ion-select-option>
              <ion-select-option value="guru">Guru</ion-select-option>
            </ion-select>
          </ion-item>
          <ion-item v-if="selectedUser">
            <ion-label>Password Baru</ion-label>
            <ion-input v-model="newPassword" type="password"></ion-input>
          </ion-item>
          <ion-button expand="block" @click="saveUser">Simpan</ion-button>
        </ion-content>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonItem,
  IonLabel,
  IonInput,
  IonButton,
  // IonList,
  IonToast,
  IonCol,
  IonRow,
  IonGrid,
  IonModal,
  IonSelect,
  IonSelectOption,
  IonButtons,
} from "@ionic/vue";
import { ref, onMounted, computed } from "vue";
import {
  getAuth,
  onAuthStateChanged,
  sendPasswordResetEmail,
  createUserWithEmailAndPassword,
  updatePassword,
} from "firebase/auth";
import {
  setDoc,
  getFirestore,
  getDocs,
  collection,
  query,
  where,
  updateDoc,
  doc,
  deleteDoc,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";
import * as XLSX from "xlsx";

const auth = getAuth(firebaseApp);
const db = getFirestore(firebaseApp);

const users = ref([]);
const searchQuery = ref("");
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const isModalOpen = ref(false);
const selectedUser = ref({}); // Inisialisasi dengan objek kosong
const newPassword = ref(""); // Password baru

const filteredUsers = computed(() => {
  return users.value.filter((user) =>
    user.fullName.toLowerCase().includes(searchQuery.value.toLowerCase())
  );
});

const fetchUsers = async () => {
  const q = query(
    collection(db, "users"),
    where("fullName", ">=", searchQuery.value),
    where("fullName", "<=", searchQuery.value + "\uf8ff")
  );

  try {
    const querySnapshot = await getDocs(q);
    users.value = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));
    console.log("Data pengguna berhasil diambil:", users.value);
  } catch (error) {
    console.error("Error fetching user data:", error);
    showToast("Terjadi kesalahan saat mengambil data pengguna", "danger");
  }
};

const filterUsers = () => {
  fetchUsers();
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchUsers();
    event.target.complete();
  }, 2000);
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const exportToExcel = () => {
  const exportData = users.value.map((user) => ({
    fullName: user.fullName,
    username: user.username,
    email: user.email,
    role: user.role,
    qrCode: user.qrCode,
    status: user.status,
  }));

  const worksheet = XLSX.utils.json_to_sheet(exportData);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Pengguna");
  XLSX.writeFile(workbook, "daftar_pengguna.xlsx");
  showToast("Data pengguna berhasil diekspor", "success");
};

const editUser = (user) => {
  selectedUser.value = { ...user };
  isModalOpen.value = true;
};

const saveUser = async () => {
  try {
    const userRef = doc(db, "users", selectedUser.value.id);
    await updateDoc(userRef, {
      fullName: selectedUser.value.fullName,
      username: selectedUser.value.username,
      email: selectedUser.value.email,
      role: selectedUser.value.role,
    });

    if (newPassword.value) {
      const user = await auth.getUser(selectedUser.value.id);
      await updatePassword(user, newPassword.value);
      newPassword.value = "";
    }

    showToast("Data pengguna berhasil diperbarui", "success");
    fetchUsers();
    closeModal();
  } catch (error) {
    console.error("Error updating user data:", error);
    showToast("Terjadi kesalahan saat memperbarui data pengguna", "danger");
  }
};

const resetPassword = async (email) => {
  try {
    await sendPasswordResetEmail(auth, email);
    showToast("Email untuk reset password telah dikirim", "success");
  } catch (error) {
    console.error("Error sending password reset email:", error);
    showToast("Terjadi kesalahan saat mengirim email reset password", "danger");
  }
};

const changeStatus = async (user) => {
  try {
    const newStatus = user.status === "aktif" ? "nonaktif" : "aktif";
    await updateDoc(doc(db, "users", user.id), { status: newStatus });
    showToast(
      `Status pengguna berhasil diubah menjadi ${newStatus}`,
      "success"
    );
    fetchUsers();
  } catch (error) {
    console.error("Error updating user status:", error);
    showToast("Terjadi kesalahan saat mengubah status pengguna", "danger");
  }
};

const deleteUser = async (userId) => {
  try {
    await deleteDoc(doc(db, "users", userId));
    showToast("Pengguna berhasil dihapus", "success");
    fetchUsers();
  } catch (error) {
    console.error("Error deleting user:", error);
    showToast("Terjadi kesalahan saat menghapus pengguna", "danger");
  }
};

const closeModal = () => {
  isModalOpen.value = false;
  selectedUser.value = {};
  newPassword.value = ""; // Reset new password field
};

const file = ref(null); // Menyimpan file Excel yang diunggah
// Fungsi menangani file yang diunggah
const handleFileUpload = (event) => {
  file.value = event.target.files[0];
};

// Fungsi untuk mengimpor data dari file Excel
const importFromExcel = async () => {
  if (!file.value) {
    showToast("Silakan pilih file Excel terlebih dahulu", "danger");
    return;
  }

  const reader = new FileReader();
  reader.onload = async (e) => {
    const data = new Uint8Array(e.target.result);
    const workbook = XLSX.read(data, { type: "array" });
    const sheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[sheetName];
    const jsonData = XLSX.utils.sheet_to_json(worksheet);

    for (let user of jsonData) {
      try {
        // Buat akun di Firebase Authentication
        const userCredential = await createUserWithEmailAndPassword(
          auth,
          user.email,
          user.password
        );

        // Dapatkan user ID dari Firebase Authentication
        const uid = userCredential.user.uid;

        // Simpan data pengguna di Firestore
        await setDoc(doc(db, "users", uid), {
          email: user.email,
          fullName: user.fullName,
          kelas: user.kelas, // Mengambil kelas dari file Excel
          nisn: user.nisn || "", // Field opsional
          profilePicture: user.profilePicture, // Mengambil URL gambar dari file Excel
          role: user.role, // Mengambil role dari file Excel
          schoolName: user.schoolName, // Mengambil nama sekolah dari file Excel
          status: user.status, // Mengambil status dari file Excel
          username: user.username,
        });

        showToast(`Pengguna ${user.fullName} berhasil ditambahkan`, "success");
      } catch (error) {
        console.error("Error adding user:", error);
        showToast(`Gagal menambahkan pengguna: ${error.message}`, "danger");
      }
    }

    fetchUsers(); // Memperbarui daftar pengguna
  };

  reader.readAsArrayBuffer(file.value);
};



onMounted(() => {
  onAuthStateChanged(auth, (user) => {
    if (user) {
      fetchUsers();
    } else {
      console.error("User not authenticated");
      showToast("Pengguna tidak terautentikasi", "danger");
    }
  });
});
</script>

<style>
/* Custom styling if needed */
.table-container {
  overflow-x: auto;
}
.table-wrapper {
  overflow-x: auto;
}
table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
  text-align: left;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
